<template>
  <div class="course-group-note">
    <head-top title="小组笔记">
    </head-top>

    <van-index-bar :index-list="groupNames" v-if="groupList && groupList.length>0">
      <div v-for="(item, index) in groupList" :key="index">
        <van-index-anchor :index="item.groupName" style="background-color: #ebf0ef;text-align: left;">{{item.groupName}}
        </van-index-anchor>
        <div v-if="item.noteList && item.noteList.length> 0" class="flex-wrap-between sub_module">
          <div v-for="(sub, k) in item.noteList" :key="k" class="image_card van-hairline--surround">
            <r-image width="42vw" height="30vw" :src="sub.imgSrc">
            </r-image>
            <div>{{sub.name}}</div>
          </div>
          <span style="width: 42vw;"></span>
        </div>
        <van-cell v-else title="暂无数据"></van-cell>
      </div>
    </van-index-bar>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import { ModulePicture } from "@/utils/enum";
import { formatDate } from "@/utils/formatData";

export default {
  name: "groupNote",
  components: { RImage },
  data () {
    return {
      groupList: [],
      groupNoteList: []
    };
  },
  computed: {
    groupNames () {
      return this.groupList.map(r => r.groupName);
    }
  },
  mounted () {
    this.getGroupList();
  },
  methods: {
    getGroupList () {
      this.groupList = [];
      this.$gatewayHttp.group.getLastGroupList().then(res => {
        if (res.Result) {
          if (res.Data && res.Data.length > 0) {
            res.Data.forEach(item => {
              this.groupList.push({
                groupName: item["GroupName"],
                groupID: item["GroupID"],
                gatewayID: item["GatewayID"],
                groupingID: item["GroupingID"],
                groupMax: item["GroupNumber"],
                studentID: item["StudentID"],
                noteList: []
              });
            });

            this.groupList.forEach((item) => {
              this.getGroupNoteList(item);
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getGroupNoteList (group) {
      group.noteList = [];
      this.$gatewayHttp.groupNote.getDataList({
        groupID: group.groupID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas) {
            res.Datas.sort((a, b) => {
              return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
            });
            res.Datas.forEach(item => {
              group.noteList.push({
                name: item["GNTtile"],
                imgSrc: this.$gatewayHttp.resourse.picture(item["GNContent"], ModulePicture.groupNote),
                createTime: formatDate(item["CreateTime"], "yyyy-MM-dd hh:mm"),
                id: item["GNID"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-group-note {
  min-height: 100%;
  color: #111;

  /deep/ .van-dropdown-menu {
    background-color: #f6f7fa;
  }

}
</style>
